@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans&family=Orbitron&display=swap);

body {
  margin: 5px;
  width: auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  background: #caf3f8;
  font-family: "Montserrat", sans-serif;
}

.title {
  color: black;
  font-weight: bold;
  position: relative;
  width: auto;
  text-align: center;
}

.markdown-input {
  background-color: black;
  border: 2px solid #117581;
  display: flex;
  width: 100%;
  height: 300px;
  padding: 10px;
  font-size: 12px;
  color: yellow;
  margin: 8px;
}

#preview {
  background-color: white;
  border: 2px solid #117581;
  width: 100%;
  height: 400px;
  padding: 10px;
  margin: 8px;
  overflow: auto;
}

@media screen and (max-width: 500px) {
  .markdown-input {
    width: 80%;
    margin: 0 auto;
  }

  #preview {
    width: 80%;
  }

  img {
    height: 100px;
  }
}

.author {
  font-family: 'Open Sans', sans-serif;
  font-size: 12px;
  width: auto;
  text-align: center;
}
